<product>
    <div>
        <form-top  :Update="access.Update" :Create="access.Create" 
        @formEdit="formEdit"
        @changeView="changeView"/>
        <div v-loading="loading">
            <el-form :inline="true" ref="productForm" :model="productForm" class="form-read-only">
                <el-form-item label="">
                    <span>{{productForm.Name}}</span>
                </el-form-item>
                <el-form-item label="">
                    <span>{{productForm.DefaultCode}}</span>
                </el-form-item>
                <el-form-item label="">
                    <span v-if="productForm.Category">{{productForm.Category.Name}}</span><span  v-else></span>
                </el-form-item>
                <el-form-item label="">
                    <span v-if="productForm.SaleOk"></span ><span v-else></span>
                </el-form-item>
                <el-form-item label="">
                    <span v-if="productForm.Active"></span ><span v-else></span>
                </el-form-item>
                <el-form-item label="">
                    <span v-if="productForm.Rental"></span ><span v-else></span>
                </el-form-item>
                <el-form-item label="">
                    <span>{{productForm.Description}}</span>
                </el-form-item>
                <el-form-item label="">
                    <span>{{productForm.DescriptionSale}}</span>
                </el-form-item>
                <el-form-item label="">
                    <span>{{productForm.DescriptionPurchase}}</span>
                </el-form-item>
                <el-form-item label="">
                    <span>{{productForm.StandardPrice}}</span>
                </el-form-item>
                <el-form-item label="">
                    <span>{{productForm.StandardWeight}}</span>
                </el-form-item>
                <el-form-item label="">
                    <span v-if="productForm.SecondSaleUom">{{productForm.SecondSaleUom.Name}}</span ><span v-else></span>
                </el-form-item>
                <el-form-item label="">
                    <span v-if="productForm.SecondSaleUom">{{productForm.SecondSaleUom.Name}}</span ><span v-else></span>
                </el-form-item>
                <el-form-item label="">
                    <span v-if="productForm.FirstPurchaseUom">{{productForm.FirstPurchaseUom.Name}}</span ><span v-else></span>
                </el-form-item>
                <el-form-item label="">
                    <span v-if="productForm.SecondPurchaseUom">{{productForm.SecondPurchaseUom.Name}}</span ><span v-else></span>
                </el-form-item>
                <el-form-item label="">
                    <span v-if="productForm.ProductType">{{productType[productForm.ProductType]}}</span><span v-else></span>
                </el-form-item>
                <el-form-item label="">
                    <span v-if="productForm.ProductMethod">{{productMethod[productForm.ProductMethod]}}</span><span v-else></span>
                </el-form-item>
                
            </el-form>
        </div>
    </div>
</product>
<script>
    import  {default as FormTop} from '@/views/common/FormTop'; 
    import  {SERVER_PRODUCT_PRODUCT} from '@/server_address';         
    import { mapState } from 'vuex';
    export default {
        data() {
            return {
                loadging:false,
                access:{
                    Create:false,
                    Update:false,
                    Read:false,
                    Unlink:false,
                },
                productForm:{},
                productType:{"stock":"","consume":"","service":""},
                productMethod:{"hand":"","auto":""}
            }
        },
        components:{
           FormTop
        },
        methods:{
            getProductProductInfo(){
                this.loadging = true;
                let id  = this.$route.params.id;
                if (id!='new'){
                    this.productForm.ID = id;
                   
                    this.$ajax.get(SERVER_PRODUCT_PRODUCT+this.productForm.ID).then(response=>{
                            this.loadging = false;
                            let {code,msg,data} = response.data;
                            if(code=='success'){
                                this.productForm = data["product"];
                                this.attributeLines = this.productForm.attributeLines;
                                this.access = data["access"];
                            }
                        });
                }else{
                    this.productForm = this.NewProductForm;
                }
            },
            changeView(type,id){
                if ("list"==type){
                    this.$router.push("/product/product");
                }else if ("form"==type){
                    this.$router.push("/product/product/form/"+id);
                }
            },
            formEdit(){
                 this.$router.push("/product/product/form/"+this.productForm.ID);
            }
        },
        created:function(){
            this.getProductProductInfo();
        },
        watch: {
            // 
            '$route': 'getProductProductInfo'
        },
         
    }
</script>
<style lang="scss" scoped>
    .values-wrapper{
        padding: 0 2px;
    }
    
</style>



