<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/admin' }"></el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/address' }"></el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/address/user' }"></el-breadcrumb-item>
        </el-breadcrumb>
        <div>
            <pagination 
            @pageInfoChange="pageInfoChange"
            :pageSize="usersData.pageSize" 
            :currentPage="usersData.currentPage"
            :total="usersData.total"/>
            <el-table
                ref="multipleTable"
                :data="usersData.userList"
                @row-dblclick = "goUserDetail"
                style="width: 100%">
                <el-table-column
                type="selection"
                width="55">
                </el-table-column>
                <el-table-column
                prop="ID"
                label="ID">
                </el-table-column>
                <el-table-column
                prop="Name"
                label="">
                </el-table-column>
                <el-table-column
                prop="NameZh"
                label="">
                </el-table-column>
                <el-table-column
                prop="Email"
                label="">
                </el-table-column>
                <el-table-column
                prop="Mobile"
                label="">
                </el-table-column>
                <el-table-column
                prop="Tel"
                label="">
                </el-table-column>
                <el-table-column
                prop="IsAdmin"
                label="">
                </el-table-column>
                .<el-table-column
                prop="Active"
                label="">
                </el-table-column>
                <el-table-column
                prop="Qq"
                label="QQ">
                </el-table-column>
                <el-table-column
                prop="WeChat"
                label="">
                </el-table-column>
                 <el-table-column
                prop="IsBackground"
                label="">
                </el-table-column>

                 
            </el-table>
        </div>
    </div>
</template>
<script>
    import  {default as Pagination} from '../common/Pagination';
    import { mapState } from 'vuex';
    export default {
      data() {
        return {
            treeViewHeight: this.$store.state.windowHeight-100,
            usersData:{
                userList:[],//tree
                pageSize:20,//
                total:0,//
                currentPage:1,//
            },
            serverUrlPath:"/setting/user"
        }
    },
    methods:{
        getUsers(limit,offset){
            this.$ajax.get(this.serverUrlPath,{
                    params:{
                        offset:offset,
                        limit:limit
                    }
                }).then(response=>{
                let {code,msg,data} = response.data;
                if(code=='success'){
                    this.usersData.userList = data["users"];
                    let paginator = data.paginator;
                    if (paginator){
                        this.usersData.total = paginator.totalCount;
                    }
                }
            });
        },
        pageInfoChange(pageSize,currentPage){
            this.usersData.pageSize = pageSize;
            this.usersData.currentPage = currentPage;
            this.getUsers(pageSize,(currentPage-1)*pageSize)
        },
        goUserDetail(row, event){
            this.$router.push("/setting/user/"+row.ID);
        }
    },
    components: {
        Pagination,
    },
    created:function(){
        this.$nextTick(function(){
            this.getUsers(this.usersData.pageSize,this.usersData.currentPage-1);
        });
    },
    computed:{
        showBottomPagitator:function(){
            return this.usersData.total/this.usersData.pageSize > 1
        }
    }
      
    }
</script>


