<template>
    <div>
        <form-top  :Update="access.Update" :Create="access.Create" 
        @formEdit="formEdit"
        @changeView="changeView"/>
        <div v-loading="loading">
            <el-form :inline="true" ref="templateForm" :model="templateForm" class="form-read-only">
                <el-form-item label="">
                    <span>{{templateForm.Name}}</span>
                </el-form-item>
                <el-form-item label="">
                    <span>{{templateForm.DefaultCode}}</span>
                </el-form-item>
                <el-form-item label="">
                    <span v-if="templateForm.Category">{{templateForm.Category.Name}}</span><span  v-else></span>
                </el-form-item>
                <el-form-item label="">
                    <span v-if="templateForm.SaleOk"></span ><span v-else></span>
                </el-form-item>
                <el-form-item label="">
                    <span v-if="templateForm.Active"></span ><span v-else></span>
                </el-form-item>
                <el-form-item label="">
                    <span v-if="templateForm.Rental"></span ><span v-else></span>
                </el-form-item>
                <el-form-item label="">
                    <span>{{templateForm.Description}}</span>
                </el-form-item>
                <el-form-item label="">
                    <span>{{templateForm.DescriptionSale}}</span>
                </el-form-item>
                <el-form-item label="">
                    <span>{{templateForm.DescriptionPurchase}}</span>
                </el-form-item>
                <el-form-item label="">
                    <span>{{templateForm.StandardPrice}}</span>
                </el-form-item>
                <el-form-item label="">
                    <span>{{templateForm.StandardWeight}}</span>
                </el-form-item>
                <el-form-item label="">
                    <span v-if="templateForm.FirstSaleUom">{{templateForm.FirstSaleUom.Name}}</span ><span v-else></span>
                </el-form-item>
                <el-form-item label="">
                    <span v-if="templateForm.FirstPurchaseUom">{{templateForm.FirstPurchaseUom.Name}}</span ><span v-else></span>
                </el-form-item>
                <el-form-item label=""  v-if="multiUnit">
                    <span v-if="templateForm.SecondSaleUom">{{templateForm.SecondSaleUom.Name}}</span ><span v-else></span>
                </el-form-item>
               
                <el-form-item label=""  v-if="multiUnit">
                    <span v-if="templateForm.SecondPurchaseUom">{{templateForm.SecondPurchaseUom.Name}}</span ><span v-else></span>
                </el-form-item>
                <el-form-item label="">
                    <span v-if="templateForm.ProductType">{{productType[templateForm.ProductType]}}</span><span v-else></span>
                </el-form-item>
                <el-form-item label="">
                    <span v-if="templateForm.ProductMethod">{{productMethod[templateForm.ProductMethod]}}</span><span v-else></span>
                </el-form-item>
                
            </el-form>
            <el-tabs  v-model="tab_active" type="card" @tab-click="handleClick">
                <el-tab-pane label="" name="attribute_lines">
                   <el-table
                        ref="caseTable"
                        :data="attributeLines"
                        style="width: 100%">
                        <el-table-column
                            prop="ID"
                            label="ID">
                        </el-table-column>
                        <el-table-column
                            label="">
                            <template scope="scope">
                                <div slot="reference" class="name-wrapper">
                                    <el-tag>{{ scope.row.Attribute.Name }}</el-tag>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column
                            label="">
                            <template scope="scope">
                                <span slot="reference" class="values-wrapper" :key="index" v-for="(attValue,index) in scope.row.AttributeValues">
                                    <el-tag>{{ attValue.Name }}</el-tag>
                                </span>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
                <el-tab-pane label="" name="template_images">
                </el-tab-pane>
                <el-tab-pane label="" name="template_products">
                </el-tab-pane>
            </el-tabs>
        </div>
    </div>
</template>
<script>
    import  {default as FormTop} from '@/views/common/FormTop'; 
    import  {SERVER_PRODUCT_TEMPLATE} from '@/server_address';         
    import { mapState } from 'vuex';
    export default {
        data() {
            return {
                loadging:false,
                tab_active:"attribute_lines",
                access:{
                    Create:false,
                    Update:false,
                    Read:false,
                    Unlink:false,
                },
                templateForm:{},
                productType:{"stock":"","consume":"","service":""},
                productMethod:{"hand":"","auto":""}
            }
        },
        components:{
           FormTop
        },
        methods:{
            showAddAttributeLineDailog(){
                this.formSave();
            },
            getProductTemplateInfo(){
                this.loadging = true;
                let id  = this.$route.params.id;
                if (id!='new'){
                    this.templateForm.ID = id;
                   
                    this.$ajax.get(SERVER_PRODUCT_TEMPLATE+this.templateForm.ID).then(response=>{
                            this.loadging = false;
                            let {code,msg,data} = response.data;
                            if(code=='success'){
                                this.templateForm = data["template"];
                                this.attributeLines = this.templateForm.attributeLines;
                                this.access = data["access"];
                            }
                        });
                }else{
                    this.templateForm = this.NewTemplateForm;
                }
            },
            changeView(type,id){
                if ("list"==type){
                    this.$router.push("/product/template");
                }else if ("form"==type){
                    this.$router.push("/product/template/form/"+id);
                }
            },
            formEdit(){
                 this.$router.push("/product/template/form/"+this.templateForm.ID);
            }
        },
        created:function(){
            this.getProductTemplateInfo();
        },
        watch: {
            // 
            '$route': 'getProductTemplateInfo'
        },
        computed:{
            ...mapState({
                multiUnit: state => state.multiUnit
            })
        }
         
    }
</script>
<style lang="scss" scoped>
    .values-wrapper{
        padding: 0 2px;
    }
    
</style>



